.home-page {
  align-items: center;
  display: flex;
  height: 84vh;
}

.home-section {
  background-color: #23232e;
  border-radius: 50px;
  color: #fff;
  font-size: 25px;
  letter-spacing: 3px;
  margin: 0 auto;
  max-width: 505px;
  padding: 30px;
  width: 70%;
}

.home-section article p {
  margin: 20px 0;
}

.home-section a {
  letter-spacing: 1px;
  font-weight: bold;
}

@media (max-width: 339px) {
  .home-section article {
    font-size: 20px;
  }
}
